	import util,{_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	import XPage from 'c%/page'
	import XFilter from 'c%/Filter.vue'
	
	const list = [
		{item: 'student_type',model: 'is_student', title: '学员类型',default: -1,
			data:[
				{did:1,title:'正式学员'},
				{did:0,title:'意向学员'}
			]
		},
		{item: 'listen_type',model: 'listen_type',title: '客户状态',default: -1,
			data:[
				{did:1,title:'排班试听'},
				{did:0,title:'跟班试听'}
			]
		},
		{com: 'SelectClass',model: 'cid',title: '班级',default: 0},
		{com: 'SelectEmployee',model: 'eid',title: '老师',default: 0},
		{com: 'SelectLesson',model: 'lid',title: '课程',default: 0},
	]
	
	export default{
		mixins: [ common,globals ],
		components: {
			XPage,
			XTable,
			XFilter
		},
		data () {
			return {
				data: [],
				pageIndex:1,
				pageSize:10,
				total: 0,
				dictList: list,
				filter: {
					name: '',
					is_student: -1,
					listen_type: -1,
					cid: 0,
					lid: 0,
					eid: 0
				}
			}
		},
		mounted () {
			this.init_data()
		},
		methods: {
			showFilter () {
				this.$refs['filter'].show()
			},
			init_data () {
				let params = {
					page: this.pageIndex,
					pagesize: this.pageSize
				}
				this.showLoading()
				this.$rest(this.data_uri).get(params)
				.success(data=>{
					this.data = data.list
					this.total = data.total
                	this.pageSize = parseInt(data.pagesize)
					this.loadFinished()
				})
			},
			pagenation (page) {
				this.pageIndex = page            
            	this.init_data()     
			},
			loadFinished () {			
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + 55 + this.headerHeight) 
			},
			filter_data () {
				let data = this.data,
					val = this.filter.name
				if(data&&data.length){
					if(!/^\s*$/.test(val)){
						let filter = (item) => {
							if(item.is_student){
								return item.student.pinyin_abbr.indexOf(val)>-1||item.student.pinyin.indexOf(val)>-1||item.student.student_name.indexOf(val)>-1								
							}else{
								return item.customer.pinyin_abbr.indexOf(val)>-1||item.customer.pinyin.indexOf(val)>-1||item.customer.name.indexOf(val)>-1
							}
						}
						return data.filter(filter)
					}
				}
				
				return this.data
			},
			data_uri () {		
				let filter = this.filter
				let params = '?'
				
				for(let i in filter){
					let val = filter[i]
					if(Array.isArray(val)){
						if(val.length){
							params += util.sprintf('%s=[In,%s]&',i,val.join(','))
						}
					}else{
						if(i=='listen_type'||i=='is_student'){
							if(val!=-1){
								params += util.sprintf('%s=%s&',i,val)
							}
						}else{
							if(val){
								params += util.sprintf('%s=%s&',i,val)
							}
						}						
					}						
				}
				params = params.substring(0,params.length-1)
				
				return 'trial_listen_arrange'+params
			},
			columns () {	
				let columns = [{
						title: '姓名',
						key: 'sname',
						width: 100,
						fixed: 'left',
						render: (h,params)=>{	
							let name = ''
							if(!_.isEmpty(params.row.customer)){
								name = params.row.customer.name 
							}
							if(!_.isEmpty(params.row.student)){
								name = params.row.student.student_name
							}
							return h('span',name)
						}
					},					
					{
						title: '学员类型',
						key: 'stype',
						width: 100,
						render: (h,params)=>{							
							if(params.row.student){
								return h('span','正式学员')
							}	
							return h('span','意向客户')
						}
					},
					{
						title: '班级名称',
						key: 'class_name',
						width: 100,
						render: (h,params)=>{							
							return h('span',params.row.one_class?params.row.one_class.class_name:'-')
						}
					},
					{
						title: '上课老师',
						key: 'eid',
						width: 100,
						render: (h,params)=>{							
							return h('span',this.$filter('ename')(params.row.eid))
						}
					},
					{
						title: '试听类型',
						key: 'listen_type',
						width: 100,
						render: (h,params)=>{
							let map_type = {0: '跟班试听',1: '排班试听'}
							return h('span',map_type[params.row.listen_type])
						}
					},
					{
						title: '总金额',
						key: 'eid',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return h('span',this.getTotal(h,'sum_total_lesson_amount'))
							}
							return h('span',this.$filter('ename')(params.row.eid))
						}
					},
					{
						title: '试听日期',
						key: 'int_day',
						width: 120,
						render: (h,params)=>{							
							return h('span',this.$filter('int_date')(params.row.int_day))
						}
					},
					{
						title: '试听日期',
						key: 'time_section',
						width: 120,
						render: (h,params)=>{							
							let start_hour = this.$filter('int_hour')(params.row.int_start_hour)
							let end_hour = this.$filter('int_hour')(params.row.int_end_hour)
							return h('span',util.sprintf('%s~%s',start_hour,end_hour))
						}
					},
					{
						title: '试听状态',
						key: 'attendance_status',
						width: 100,
						render: (h,params)=>{		
							let row = params.row,
							result = '',
							classes = 'text-success'
							
							if(row.is_attendance===1){
								if(row.attendance_status===1){
									result = '已试听'
								}else{
									classes = 'text-danger'
									result = '缺勤'
								}
							}else{
									classes = 'text-primary'
									result = '待试听'
								}
								return h('span',{ 'class':classes },result)
							}
					}]
					
				return columns
			},
		}
	}



